import React from 'react'
import PropTypes from 'prop-types'
import { Helmet } from 'react-helmet'

/* eslint-disable max-lines-per-function */
// Borrowed from Gatsby template
function SEO({ title, description, lang, keywords, twitterAuthor }) {
  return (
    <Helmet
      htmlAttributes={{
        lang
      }}
      title={title}
      meta={[
        {
          name: `description`,
          content: description
        },
        {
          property: `og:title`,
          content: title
        },
        {
          property: `og:description`,
          content: description
        },
        {
          property: `og:type`,
          content: `website`
        },
        {
          name: `twitter:card`,
          content: `summary`
        },
        {
          name: `twitter:creator`,
          content: twitterAuthor
        },
        {
          name: `twitter:title`,
          content: title
        },
        {
          name: `twitter:description`,
          content: description
        }
      ].concat(
        keywords.length > 0
          ? {
              name: 'keywords',
              content: keywords.join(', ')
            }
          : []
      )}
    />
  )
}
/* eslint-enable max-lines-per-function */

SEO.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string,
  lang: PropTypes.string,
  keywords: PropTypes.arrayOf(PropTypes.string),
  twitterAuthor: PropTypes.string
}

SEO.defaultProps = {
  lang: 'en',
  description: '',
  twitterAuthor: 'tamed_lionheart',
  keywords: []
}

export default SEO
